<template>
  <div class="am-grid am-grid-square " :class="{ 'am-grid-line': border }">
    <slot></slot>
</div>
</template>

<script>
/**
 * mt-header
 * @module components/header
 * @desc 
 * @param {boolean} [fixed=false] - 
 * @param {string} [title] - 
 * @param {slot} [left] - 
 * @param {slot} [right] - 
 *
 * @example
 * <mt-header title="" fixed>
 *   <mt-button slot="left" icon="back" @click="handleBack"></mt-button>
 *   <mt-button slot="right" icon="more"></mt-button>
 * </mt-header>
 */
export default {
  name: 'mt-grid',
  props: {
    border: Boolean,
  }
};
</script>
<style lang="css">
 
</style>



